import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { Button } from "@react-native-material/core";
import { View } from "react-native";
import { layoutConstants } from "../../models/contants";

export default function QuickTask({ navigation }) {
  return (
    <View
      style={{ marginHorizontal: "15%", borderRadius: 10, marginBottom: 10, opacity: 0.4, borderBottomWidth: 1, borderBottomColor: 'gray' }}
    >
      <Button
        variant="outlined"
        title="Add Action"
        onPress={() => {
          navigation.navigate("ActionDetail", {
            back: { component: "Home", autoFocus: true },
          });
        }}
        leading={(props) => (
          <Icon name="pencil" {...props} color={layoutConstants.textColor} />
        )}
        titleStyle={{ color: layoutConstants.textColor, fontSize: 12 }}
        contentContainerStyle={{
          backgroundColor: layoutConstants.bgColorSecondary,
        }}
        style={{ justifyContent: "flex-start" }}
      />
    </View>
  );
}
