import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { AppBar, IconButton } from "@react-native-material/core";
import React from "react";
import { layoutConstants } from "../../models/contants";
import FAB from "./fab";

const BottomBar = (props) => {
  const { onMenuClick, onAddClick } = props;
  return (
    <AppBar
      variant="bottom"
      color={layoutConstants.colorPrimary}
      enableColorOnDark={true}
      leading={(props) => (
        <IconButton
          contentContainerStyle={{ left: 15 }}
          onPress={onMenuClick}
          color={"white"}
          icon={(props) => <Icon  name="menu" {...props} color={"white"} />}
          {...props}
        />
      )}
      //   trailing={(props) => (
      //     <IconButton
      //       icon={(props) => <Icon name="magnify" {...props} />}
      //       {...props}
      //     />
      //   )}
      style={{ position: "absolute", start: 0, end: 0, bottom: 0 }}
    >
      <FAB
        color={layoutConstants.colorPrimary}
        tintColor={"white"}
        onPress={onAddClick}
        icon={(props) => <Icon name="plus" {...props} />}
        style={{ position: "absolute", top: -28, alignSelf: "center", borderColor: "white", borderWidth: 1 }}
      />
    </AppBar>
  );
};

export default BottomBar;
