import { log } from "utils/log";
import TaskCategoryService from "services/task-category-service";
import TaskService from "services/task-service";

export default class RemoveItemService {
  taskService;
  categoryService;
  constructor() {
    this.taskService = new TaskService();
    this.categoryService = new TaskCategoryService();
  }

  removeAble(item) {
    if (this.isTaskModel(item) || this.isTaskCategoryModel(item)) {
      return true;
    }
    return false;
  }

  isTaskModel(item) {
    if (item.constructor.name === "TaskModel") {
      return true;
    }
    return false;
  }

  isTaskCategoryModel(item) {
    if (item.constructor.name === "TaskCategoryModel") {
      return true;
    }
    return false;
  }

  undo(item) {
    log.debug("undo", item);
    if (this.removeAble(item)) {
      return;
    }
  }

  async removePermanent(item) {
    log.debug("removePermanent", item);
    // validate item
    if (!this.removeAble(item)) {
      return;
    }
    if (this.isTaskModel(item)) {
      const reItem = await this.taskService.removeTaskByCategory(
        item.categoryId,
        item
      );
      log.info("removePermanent success");
      return reItem;
    }
    if (this.isTaskCategoryModel(item)) {
      const reItem = await this.categoryService.removeCategories(item);
      log.info("removePermanent success");
      return reItem;
    }
    log.info("removePermanent failed");
  }

  async undoRemoved({item, dispatch}) {
    log.debug("undoRemoved", item);
    // validate item
    if (!this.removeAble(item)) {
      return;
    }
    if (this.isTaskModel(item)) {
      const result = await this.taskService.undoRemovedTask({
        item,
        dispatch,
      });
      log.info("undoRemoved success");
      return result.item;
    }
    if (this.isTaskCategoryModel(item)) {
      const result = await this.categoryService.undoRemovedCategory({
        item,
        dispatch,
      });
      log.info("undoRemoved success");
      return result.item;
    }
    log.info("undoRemoved failed");
  }
}

let removeItemService;
const createRemoveItemService = () => {
  if (removeItemService) {
    return removeItemService;
  }
  removeItemService = new RemoveItemService();
  return removeItemService;
};

export { createRemoveItemService };
