import { Button, Text } from "@react-native-material/core";
import { layoutConstants } from "../../models/contants";
import { StyleSheet, View } from "react-native";
import HomeTaskList from "./task-list";

export default function RecentTasks({ tasks, navigation }) {
  const onPressTaskList = () => {
    navigation.navigate("Actions");
  };

  return (
    tasks?.length > 0 && (
      <View style={styles.body}>
        <HomeTaskList
          tasks={tasks.reverse()}
          onPress={onPressTaskList}
        ></HomeTaskList>
        <Button
          title="See more actions"
          variant="text"
          onPress={onPressTaskList}
          color={layoutConstants.textColor}
          titleStyle={{ fontSize: 12 }}
        />
      </View>
    )
  );
}

const styles = StyleSheet.create({
  body: {
    borderTopStartRadius: 30,
    borderTopRightRadius: 30,
    padding: 5,
    backgroundColor: layoutConstants.bgColorPrimary,
  },
  container: {
    backgroundColor: layoutConstants.bgColorPrimary,
    flex: 1,
    color: layoutConstants.colorPrimary,
  },
  daily: {
    height: 300,
    padding: 5,
    color: layoutConstants.textColorSecondary,
  },
  textDaily: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColor,
    fontSize: layoutConstants.quoteSize,
  },
});
