import DateTimeModel from "../date-time/date-time-model";
import DayRepeatModel from "../day-repeat-model";

class TaskRepeatMediator {
  dayRepeat;
  dateModel;
  timeModel;
  displayChip;
  date;
  time;
  selectedDaysRepeat;

  constructor(dayRepeat, dateModel, timeModel) {
    this.dayRepeat = dayRepeat;
    this.dateModel = dateModel;
    this.timeModel = timeModel;
  }

  process() {
    // todo do we need try catch?
    if(!this.dayRepeat) {
      return;
    }
    if (this.dayRepeat.isDaily() && this.timeModel) {
      this.displayChip = this.timeModel.getHoursMinutes() + " Daily";
      this.time = this.timeModel.getDate();
    }
    if (this.dayRepeat.isMonthly()) {
      this.displayChip = "Monthly";
    }
    if (this.dayRepeat.isNormalRepeat() && this.timeModel) {
      this.displayChip =
        this.timeModel.getHoursMinutes() +
        ` ${this.dayRepeat.selected.length} days`;
      this.time = this.timeModel.getDate();
    }
    if (this.dayRepeat.isNoRepeat() && this.timeModel && this.dateModel) {
      this.displayChip =
        this.timeModel.getHoursMinutes() +
        ` ${this.dateModel.getDateString()}`;
      this.date = this.dateModel.getDate();
      this.time = this.timeModel.getDate();
    }
    this.selectedDaysRepeat = this.dayRepeat.getSelectedValue();
  }
}

class TaskRepeatRawDataMediator extends TaskRepeatMediator {
  constructor(repeat, date, time) {
    super(
      repeat? new DayRepeatModel(repeat) : null,
      date? new DateTimeModel(date): null,
      time? new DateTimeModel(time): null
    );
  }
}

export { TaskRepeatMediator, TaskRepeatRawDataMediator };
