import { date, object, string, array } from "yup";

const TaskValidator = object({
  id: string().required(),
  title: string().required(),
  categoryId: string().required(),
  description: string().nullable(),
  selectedDaysRepeat: array().of(string()).nullable(),
  date: date().nullable(),
  time: date().nullable(),
  createdDate: date().nullable(),
  updatedDate: date().nullable(),
});
const TaskListValidator = array().of(TaskValidator);
export { TaskValidator, TaskListValidator };
