import { configureStore } from '@reduxjs/toolkit'
import categorySliceReducer from './features/category-slice'
import counterSliceReducer from './features/couterSlice'
import taskSliceReducer from './features/task-slice'
import activeCategorySliceReducer from './features/active-category-slice'

// only for reactive programing
export default configureStore({
  reducer: {
    counter: counterSliceReducer,
    task: taskSliceReducer,
    category: categorySliceReducer,
    activeCategory: activeCategorySliceReducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false
    })
})