import uuid from 'react-native-uuid';

export default class TaskModel {
  id;
  title;
  categoryId;
  description;
  selectedDaysRepeat;
  date;
  time;
  deleted;
  createdDate;
  updatedDate;

  constructor(taskJson) {
    const {id, title, description, selectedDaysRepeat, date, time, deleted, categoryId, createdDate, updatedDate} = taskJson;
    this.id = id ? id : uuid.v4();
    this.title = title;
    this.categoryId = categoryId;
    this.description = description;
    this.selectedDaysRepeat = selectedDaysRepeat;
    this.date = date;
    this.time = time;
    this.deleted = deleted;
    this.createdDate = createdDate || new Date();
    this.updatedDate = updatedDate || new Date();
  }
}
