import React, { useEffect } from "react";
import { StyleSheet, View } from "react-native";
import { layoutConstants } from "../../models/contants";
import { constants } from "../../services/constant";
import { getLinkByDate, requestS3ByUrl } from "../../services/utils";
import BoxLabel from "./box-label";
import TitleBoxs from "./title-boxs";

export default function TrendingQuote({ navigation }) {
  const [trendingQuotes, setTrendingQuote] = React.useState([]);
  const [linkTrendingQuote, setlinkTrendingQuote] = React.useState(null);
  useEffect(() => {
    const getDailyTrendingQuote = async (link) => {
      const trendingQuote = await requestS3ByUrl(link);
      setTrendingQuote(trendingQuote.contents);
    };

    const link = getLinkByDate(constants.BASE_TRENDING_QUOTE_URL);
    setlinkTrendingQuote(link);
    getDailyTrendingQuote(link);
  }, []);

  const constantsImagesThumbnails = [
    "assets/quote/thumbnails/thumbnail_001.jpg",
    "assets/quote/thumbnails/thumbnail_002.jpg",
    "assets/quote/thumbnails/thumbnail_003.jpg",
    "assets/quote/thumbnails/thumbnail_004.jpg",
    "assets/quote/thumbnails/thumbnail_005.jpg",
    "assets/quote/thumbnails/thumbnail_006.jpg",
    "assets/quote/thumbnails/thumbnail_007.jpg",
    "assets/quote/thumbnails/thumbnail_008.jpg",
    "assets/quote/thumbnails/thumbnail_009.jpg",
    "assets/quote/thumbnails/thumbnail_010.jpg",
  ];

  const quotes = trendingQuotes.slice(0, 10);
  const onPressHandler = (index) => {
    navigation.navigate("QuoteDetail", { url: linkTrendingQuote, current: index });
  };
  return (
    <View>
      <TitleBoxs title={"Trending"}>
        {quotes?.length > 0 &&
          quotes.map((item, index) => {
            return (
              <BoxLabel
                awsUrl={constantsImagesThumbnails[index]}
                key={index}
                style={styles.treding}
                textStyle={styles.textStyle}
                label={item.item}
                onPress={() => onPressHandler(index)}
              ></BoxLabel>
            );
          })}
      </TitleBoxs>
    </View>
  );
}

const styles = StyleSheet.create({
  treding: {
    width: 300,
    height: "45%",
  },
  textStyle: {
    width: 220,
    margin: 10,
    color: layoutConstants.textColor,
  },
});
