import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { taskDao } from "models/task/task-dao";
import { TaskListValidator } from "models/task/task-validator";
import { log } from "utils/log";
import { constants } from "../models/contants";
import { TaskListModel } from "../models/task/task-list-model";
import { TaskValidator } from "../models/task/task-validator";

export const taskSlice = createSlice({
  name: "task",
  initialState: {
    value: [],
  },
  reducers: {
    setTasks: (state, action) => {
      log.debug("taskSlice setTasks", action);
      state.value = action.payload;
    },
  }
});

export const saveTasksByCategory = createAsyncThunk(
  "task/saveTasksByCategory",
  async (payload, { dispatch }) => {
    const { categoryId, tasks } = payload;
    await TaskListValidator.validate(tasks);
    await taskDao.setTasksByCategory(categoryId, JSON.stringify(tasks));
    dispatch(setTasks(tasks));
  }
);

export const saveRecent = createAsyncThunk(
  "task/saveRecent",
  async (payload) => {
    const { recent, isUpdate } = payload;
    await TaskValidator.validate(recent);
    let rawTasks = JSON.parse(
      await taskDao.getTasksByCategory(constants.RECENT_ACTIONS)
    ) || [];
    if(isUpdate || rawTasks.some(task => task.id === recent.id)) {
      rawTasks = rawTasks.filter(task => task.id !== recent.id);
    }
    if(rawTasks.length >= constants.RECENT_SIZE) {
      rawTasks = rawTasks.slice(1, constants.RECENT_SIZE);
    }
    rawTasks.push(recent);
    await taskDao.setTasksByCategory(constants.RECENT_ACTIONS, JSON.stringify(rawTasks));
  }
);

export const getTasksByCategory = createAsyncThunk(
  "task/getTasksByCategory",
  async (payload, { dispatch }) => {
    const { categoryId, includeDeleted } = payload;
    try {
      let rawTasks = JSON.parse(
        await taskDao.getTasksByCategory(categoryId)
      );
      if(!rawTasks) {
        throw Error('Empty Task for Category')
      }
      const taskList = new TaskListModel();
      taskList.set(rawTasks);
      // dispatch(setActiveCategory(category));
      dispatch(setTasks(taskList.get(includeDeleted)));
    } catch (error) {
      log.warn(error);
      dispatch(setTasks([]));
    }
  }
);

// Action creators are generated for each case reducer function
export const { setTasks } = taskSlice.actions;

const taskSliceReducer = taskSlice.reducer;
export default taskSliceReducer;
