import AsyncStorage from "@react-native-async-storage/async-storage";

export default class DaoBase {
  async getItem(inputKey) {
    return await AsyncStorage.getItem(inputKey);
  }

  async setItem(inputKey, inputValue) {
    await AsyncStorage.setItem(inputKey, inputValue);
  }

  async mergeItem(inputKey, inputValue) {
    await AsyncStorage.mergeItem(inputKey, inputValue);
  }

  async multiSet(value) {
    await AsyncStorage.multiSet(value);
  }

  async clear() {
    await AsyncStorage.clear();
  }
}
