import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { IconButton, Text } from "@react-native-material/core";
import React from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import TabListModel from "models/tab-list-model";
import ButtonPressable from "../utils/button-pressable";
import { layoutConstants } from "../../models/contants";

// data model: tabs
// view props: others
// action: onAdd, onClick
export default function CategoryTab(props) {
  const {
    tabs,
    activeCategory,
    onAdd,
    onClick,
    onDelete,
    onUpdate,
    onLongPress,
    ...others
  } = props;
  const tabList = new TabListModel(tabs);
  const onClickHandler = (tab) => {
    onClick(tab);
  };
  return (
    <View style={styles.tabContainer}>
      {/* <IconButton
        onPress={onShowSheet}
        icon={(props) => <Icon name="menu" {...props} />}
      /> */}
      {tabList.tabs && activeCategory && (
        <ScrollView horizontal style={{ flexGrow: 0 }}>
          <View style={{ display: "flex", flexDirection: "row" }}>
            {tabList.tabs.map((tab, index) => {
              const active = tab.id === activeCategory.id;
              return (
                <ButtonPressable
                  key={index}
                  onPress={() => {
                    onClickHandler(tab);
                  }}
                  onLongPress={() => {
                    onClickHandler(tab);
                    onLongPress();
                  }}
                  customStyles={
                    active
                      ? { ...styles.button, ...styles.active }
                      : { ...styles.button }
                  }
                  active={active}
                >
                  <Text
                    numberOfLines={1}
                    style={active ? styles.textActive : styles.text}
                  >
                    {tab.title}
                  </Text>
                </ButtonPressable>
              );
            })}
          </View>
        </ScrollView>
      )}
      <IconButton
        key="add-category"
        onPress={onAdd}
        icon={(props) => (
          <Icon name="plus" {...props} color={layoutConstants.textColor}/>
        )}
      />
    </View>
  );
}
const styles = StyleSheet.create({
  button: {
    width: 80,
  },
  buttonMenu: {
    width: 60,
  },
  tabContainer: {
    display: "flex",
    flexDirection: "row",
    backgroundColor: "white",
    borderBottomWidth: 2,
    borderBottomColor: "rgb(216, 216, 216)",
  },
  buttonActive: {
    width: 120,
    borderColor: layoutConstants.colorPrimary,
    borderBottomWidth: 2,
  },
  textActive: {
    color: layoutConstants.colorPrimary,
  },
  text: {
    color: layoutConstants.textColor,
  },
  active: {
    textColor: layoutConstants.colorPrimary,
    borderColor: layoutConstants.colorPrimary,
    borderBottomWidth: 3,
    borderRadius: 3,
  },
});
