import { constants } from "../contants";
import TaskModel from "./task-model";

export class TaskListModel {
  tasks = [];

  get(includeDeleted = false) {
    if (!includeDeleted) {
      return this.tasks.filter((task) => {
        return !task.deleted;
      });
    }
    return this.tasks;
  }

  add(task) {
    this.tasks.push(task);
    return this.get(false);
  }

  softDelete(value) {
    let index = -1;
    this.tasks = this.tasks.map((task, i) => {
      if (task.id == value.id) {
        task.deleted = true;
        index = i;
      }
      return task;
    });
    return index;
  }

  set(rawTasks) {
    if(rawTasks) {
      this.tasks = rawTasks.map((taskJson) => {
        const task = new TaskModel(taskJson);
        if (!task.categoryId) {
          task.categoryId = constants.MY_ACTIONS;
        }
        return task;
      });
    }
  }

  remove(value) {
    this.tasks = this.tasks.filter((task) => {
      return task.id !== value.id && task.deleted !== true;
    });
  }

  undo(value) {
    this.tasks = this.tasks.map(task => {
      if(task.id === value.id) {
        task.deleted = false;
      }
      return task;
    });
  }

  update(task) {
    this.tasks = this.tasks.map((t) => {
      if (t.id === task.id) {
        return task;
      } else {
        return t;
      }
    });
    return this.get(false);
  }
}
