import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { Button, Pressable, Text } from "@react-native-material/core";
import * as React from "react";
import { StyleSheet, View } from "react-native";
import DayRepeatModel from "models/day-repeat-model";
import ExpoCheckbox from "../utils/expo-checkbox";

export default function TimeRepeatComponent(props) {
  const { repeat, onChange } = props;
  const [dayRepeat, setDayRepeat] = React.useState(new DayRepeatModel(repeat));
  const [repeatValue, setRepeatValue] = React.useState(dayRepeat.getLabel());
  const [show, setShow] = React.useState(dayRepeat.getShowRepeat());

  const repeatButtonClick = () => {
    setShow(!show);
  };

  const selectRepeatLabel = () => {
    setRepeatValue(dayRepeat.getLabel());
  };

  const onCheck = (checked, value) => {
    if (["no_repeat", "monthly"].includes(value)) {
      dayRepeat.days.map((day) => {
        if (day.value === value) {
          day.checked = checked;
        } else {
          day.checked = false;
        }
        return day;
      });
    } else {
      dayRepeat.days[0].checked = false;
      dayRepeat.days[2].checked = false;
      let values = [value];
      if (value === "daily") {
        values = ["daily", "mon", "tue", "wed", "thu", "fri", "sat", "sun"];
      }
      dayRepeat.days.map((day) => {
        if (values.includes(day.value)) {
          day.checked = checked;
        }
        return day;
      });
      // check daily if all days checked
      dayRepeat.days[1].checked = dayRepeat.days
        .slice(3)
        .every((day) => day.checked);
    }
    dayRepeat.setSelected();
    const newRepeat = dayRepeat.getSelectedValue();
    const newDayRepeat = new DayRepeatModel(newRepeat);
    setDayRepeat(newDayRepeat);
    selectRepeatLabel();
    onChange(newDayRepeat);
  };

  return (
    <View style={{ display: "flex", flexDirection: "column" }}>
      <Button
        title={repeatValue}
        onPress={repeatButtonClick}
        leading={(props) => <Icon name="repeat" {...props} />}
        style={{ margin: 10 }}
      />
      {show && (
        <View
          style={{ display: "flex", flexDirection: "row", flexWrap: "wrap" }}
        >
          {dayRepeat &&
            dayRepeat.days.map((day) => {
              return (
                <View
                  key={day.value}
                  style={{ width: "50%", alignItems: "flex-start" }}
                >
                  <Pressable
                    onPress={() => {
                      onCheck(!day.checked, day.value);
                    }}
                    style={{
                      display: "flex",
                      flexDirection: "row",
                      margin: 10,
                    }}
                  >
                    <ExpoCheckbox
                      style={styles.checkbox}
                      value={day.checked}
                      keyword={day.value}
                      onValueChange={onCheck}
                    />
                    <Text>{day.title}</Text>
                  </Pressable>
                </View>
              );
            })}
        </View>
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  checkbox: {
    marginHorizontal: 10,
  },
});
