
export default class DayRepeatModel {
  days = [
    { title: "No Repeat", value: "no_repeat", checked: false },
    { title: "Daily", value: "daily", checked: false },
    { title: "Monthly", value: "monthly", checked: false },
    { title: "Mon", value: "mon", checked: false },
    { title: "Tue", value: "tue", checked: false },
    { title: "Wed", value: "wed", checked: false },
    { title: "Thu", value: "thu", checked: false },
    { title: "Fri", value: "fri", checked: false },
    { title: "Sat", value: "sat", checked: false },
    { title: "Sun", value: "sun", checked: false },
  ];
  selected = [];

  constructor(selectedDays) {
    if(!selectedDays) {
      selectedDays = ["no_repeat"]
    }
    this.days = this.days.map(day => {
      if(selectedDays.includes(day.value)) {
        day.checked = true;
      }
      return day;
    });
    this.setSelected();
  }

  setSelected() {
    this.selected = this.days.filter((day) => day.checked);
  }

  getSelectedValue() {
    return this.selected.reduce((acc, curr) => {
      acc.push(curr.value);
      return acc;
    }, [])
  }

  getLabel() {
    const selected = this.selected;
    if (this.isNoRepeat()) {
      return "No Repeat";
    }
    if (selected.length === 1) {
      return "Repeat " + selected[0].title;
    } else {
      if (this.isDaily()) {
        return "Repeat Daily";
      } else {
        return "Repeat " + selected.length + " days";
      }
    }
  }

  getHideDate() {
    if(this.isNoRepeat()) {
      return false;
    }
    return true;
  }

  getHideTime() {
    if(this.isMonthly()) {
      return true;
    }
    return false;
  }

  getShowRepeat() {
    return this.selected.length > 0;
  }

  isNoRepeat() {
    if(this.selected.length === 0) {
        return true;
    } else {
        return (this.selected.length === 1 && this.selected[0].value === 'no_repeat') ? true : false;
    }
  }

  isDaily() {
    return this.selected.some(day => day.value === 'daily');
  }

  isMonthly() {
    return (this.selected.length === 1 && this.selected[0].value === 'monthly') ? true : false;
  }

  isNormalRepeat() {
    if(this.selected.length === 1) {
        return this.selected[0].value !== 'monthly' && this.selected[0].value !== 'no_repeat';
    }
    if(this.selected.length > 1 && this.selected.length < 8) {
        return true
    }
    return false;
  }
}
