import { Button } from "@react-native-material/core";
import React, { useRef } from "react";
import { useEffect } from "react";
import { View } from "react-native";
import ActionSheet from "react-native-actions-sheet";
import { Divider } from "@react-native-material/core";

let isUpdate = false;
export default function CategoryActionSheet(props) {
  const { activeCategory, onDelete, onUpdate, visible, setVisible } = props;
  const isDefaultCategory = activeCategory?.id === "my-task";
  const actionRef = useRef(null);
  const onShow = () => {
    isUpdate = false;
    actionRef.current?.show();
  };
  useEffect(() => {
    if (visible) {
      onShow();
    } else {
      actionRef.current?.hide();
    }
  }, [visible]);

  const onDeleteHandler = () => {
    isUpdate = false;
    actionRef.current?.hide();
    onDelete(activeCategory);
  };

  const onUpdateHandler = () => {
    isUpdate = true;
    actionRef.current?.hide();
  };

  const onCloseHander = () => {
    if (isUpdate) {
      onUpdate(activeCategory);
    }
    setVisible(false);
  };

  return (
    <ActionSheet ref={actionRef} onClose={onCloseHander}>
      <View style={{ margin: 20, display: "flex" }}>
        <Button
          variant="text"
          key="remove-category"
          onPress={onDeleteHandler}
          disabled={isDefaultCategory}
          title="Remove Category"
        />
        <Divider style={{ margin: 5 }} />
        <Button
          variant="text"
          key="update-category"
          onPress={onUpdateHandler}
          disabled={isDefaultCategory}
          title="Update Category"
        />
      </View>
    </ActionSheet>
  );
}
