import { log } from "utils/log";

import { requestS3ByUrl } from "./utils";
import { Storage } from "aws-amplify";

export default class QuoteService {
  async getList(url) {
    log.debug("getList", url);
    return await requestS3ByUrl(url)
  }

  async getBgUrl(url) {
    log.debug("getBgUrl", url);
    return await Storage.get(url);
  }
}

let service;
const createQuoteService = () => {
  if (service) {
    return service;
  }
  service = new QuoteService();
  return service;
};

export { createQuoteService };


