import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import {
  Button,
  DialogActions,
  DialogContent,
  DialogHeader,
  IconButton
} from "@react-native-material/core";
import * as React from "react";
import { View } from "react-native";
import DateTimeModel from "models/date-time/date-time-model";
import DayRepeatModel from "models/day-repeat-model";
import Dialog from "../utils/dialog";
import HideKeyboard from "../utils/hide-keyboard";
import TaskDateTimePicker from "./task-date-time-picker";
import TimeRepeatComponent from "./time-repeat-component";
let newDayRepeat;
let newDate;
let newTime;
// data model: repeat, date, time
// view props: visible
// action: onClose, onDone
export default function TaskTimeDialog(props) {
  // on init create form, task date, task time, task repeat are default
  // on init update form, task date, task time, task repeat are updated base on data model
  // on action done form, task date, task time, task repeat are submited
  // on action close form, close form
  // visible, date, time, repeat, onClose, onDone
  const dateTimeModel = new DateTimeModel();
  const { visible, repeat, date, time, onClose, onDone } = props;
  const [hideDate, setHideDate] = React.useState(false);
  const [hideTime, setHideTime] = React.useState(false);
  React.useEffect(() => {
    if (visible === true) {
      newDayRepeat = new DayRepeatModel(repeat);
      newDate = date;
      newTime = time;
      hideDateTime(newDayRepeat);
    }
  }, [props.visible]);

  // on init task time
  // on action chip click
  // on action add time
  const hideDateTime = (dayRepeat) => {
    setHideDate(dayRepeat.getHideDate());
    setHideTime(dayRepeat.getHideTime());
  };

  // TimeRepeatComponent
  const onTimeRepeatChangeHandler = (dayRepeat) => {
    // setNewDayRepeat(dayRepeat);
    newDayRepeat = dayRepeat;
    hideDateTime(newDayRepeat);
  };
  // End TimeRepeatComponent

  // TaskDateTimePicker
  const onDateChangeHandler = (event, selectedDate) => {
    newDate = selectedDate;
  };

  const onTimeChangeHandler = (event, selectedTime) => {
    newTime = selectedTime;
  };
  // End TaskDateTimePicker

  const onDoneHandler = () => {
    onDone(
      newDayRepeat,
      new DateTimeModel(newDate),
      new DateTimeModel(newTime)
    );
  };

  return (
    <Dialog visible={visible}>
      <HideKeyboard>
        <View>
          <View style={{ display: "flex", flexDirection: "row" }}>
            <View style={{ flex: 1 }}>
              <DialogHeader title="Time" />
            </View>
            <IconButton
              onPress={onClose}
              icon={(props) => <Icon name="close" {...props} />}
              {...props}
            />
          </View>
          <DialogContent>
            <TimeRepeatComponent
              repeat={repeat}
              onChange={onTimeRepeatChangeHandler}
            ></TimeRepeatComponent>
            <TaskDateTimePicker
              date={date || dateTimeModel.getDate()}
              time={time || dateTimeModel.getDate()}
              hideDate={hideDate}
              hideTime={hideTime}
              onDateChange={onDateChangeHandler}
              onTimeChange={onTimeChangeHandler}
            ></TaskDateTimePicker>
          </DialogContent>

          <DialogActions>
            <Button title="Done" onPress={onDoneHandler} />
          </DialogActions>
        </View>
      </HideKeyboard>
    </Dialog>
  );
}
