import { Storage } from "aws-amplify";
import axios from "axios";
import { log } from "utils/log";

export async function requestS3ByUrl(url, timeout = 5000) {
  log.debug("url from requestS3ByUrl", url);
  const awsUrl = await Storage.get(url);
  const request = axios.create({ baseURL: awsUrl, timeout: timeout });
  const data = (await request.get(awsUrl)).data;
  log.debug("requestS3ByUrl", data);
  return data;
}

export async function requestUrlS3(url) {
  log.debug("url from requestS3ByUrl", url);
  return await Storage.get(url);
}

export function getTodayObject() {
  log.debug("getTodayObject");
  const date = new Date();
  return date;
}

export function getLinkByDate(base_url, date) {
  log.debug("getLinkByDate");
  if (date == null) {
    date = new Date();
  }
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toString().padStart(2, "0"); // add leading zero if necessary
  const day = date.getDate().toString().padStart(2, "0"); // add leading zero if necessary

  const formattedYearMonth = `${year}${month}`;
  const formattedYearMonthDate = `${year}${month}${day}`;
  log.debug(
    base_url + formattedYearMonth + "/" + formattedYearMonthDate + ".json"
  );
  return base_url + formattedYearMonth + "/" + formattedYearMonthDate + ".json";
}

export async function requestUrlS3ByDate(base_url, date = null) {
  log.debug("requestUrlS3ByDate");
  const link_this_date = await getLinkByDate(base_url, date);

  log.debug(link_this_date);
  return await requestS3ByUrl(link_this_date);
}

export function randomWithinArray(array) {
  const randomIndex = Math.floor(Math.random() * array.length);
  return array[randomIndex];
}
