import { Pressable, StyleSheet, Text } from "react-native";

export default function ButtonPressable(props) {
  const { onPress, children, active, customStyles, ...others } = props;
  return (
    <Pressable
      onPress={onPress}
      style={({ pressed }) => [
        styles.button,
        pressed? styles.buttonPressed : "",
        customStyles,
      ]}
      {...others}
    >
      {children}
    </Pressable>
  );
}

const styles = StyleSheet.create({
  buttonPressed: {
    opacity: 0.5,
  },
  button: {
    padding: 10,
    textAlign: "center",
    overflow: "hidden",
    textOverflow: "ellipsis"
  }
});
