import { createSlice } from '@reduxjs/toolkit'
import { log } from "utils/log";

export const activeCategorySlice = createSlice({
  name: 'activeCategory',
  initialState: {
    value: null,
  },
  reducers: {
    setActiveCategory: (state, action) => {
      log.debug("setActiveCategory", action);
      state.value = action.payload;
    },
  },
})

// Action creators are generated for each case reducer function
export const { setActiveCategory } = activeCategorySlice.actions

const activeCategorySliceReducer = activeCategorySlice.reducer;
export default activeCategorySliceReducer;