import DaoBase from "../dao-base";

export default class TaskDao extends DaoBase {
  async getTasksByCategory(categoryId) {
    const key = this.getKeyPrefix() + categoryId;
    return await this.getItem(key);
  }

  async setTasksByCategory(categoryId, value) {
    const key = this.getKeyPrefix() + categoryId;
    await this.setItem(key, value);
  }

  async mergeTaskByCategory(categoryId, value) {
    const key = this.getKeyPrefix() + categoryId;
    await this.mergeItem(key, value);
  }

  getKeyPrefix() {
    return "tasks-category-";
  }
}

export const taskDao = new TaskDao();
