import DaoBase from "../dao-base";

export default class TaskCategoryDao extends DaoBase {
  async getCategories() {
    const key = this.getKeyPrefix();
    return await this.getItem(key);
  }

  async setCategories(value) {
    const key = this.getKeyPrefix();
    await this.setItem(key, value);
  }

  getKeyPrefix() {
    return "categories";
  }
}

export const taskCategoryDao = new TaskCategoryDao();