export default class TabListModel {
    tabs = [];
    constructor(tabs) {
        if(tabs) {
            this.tabs = tabs;
        }
        this.setDefaultActiveTab();
    }

    setDefaultActiveTab() {
        if(this.tabs.length > 0 && !this.tabs.some(tab => tab.active)) {
            this.tabs[0].active = true;
        }
    }
  }
  