import * as React from "react";
import { Text } from "@react-native-material/core";
import { View } from "react-native";
import DateTimePicker from "@react-native-community/datetimepicker";

export default function TaskDateTimePicker(props) {
  const { onDateChange, onTimeChange, date, time, hideDate, hideTime } = props;

  const onDateChangeHandler = (event, selectedDate) => {
    onDateChange(event, selectedDate);
  }

  const onTimeChangeHandler = (event, selectedTime) => {
    onTimeChange(event, selectedTime);
  }

  return (
    <View>
      {!hideDate && (
        <View
          key="date-picker"
          style={{
            display: "flex",
            flexDirection: "row",
            marginVertical: 10,
          }}
        >
          <View style={{ width: "40%", margin: 7 }}>
            <Text>Date:</Text>
          </View>
          <View style={{ width: "40%" }}>
            <DateTimePicker
              testID="dateTimePicker"
              value={date}
              mode={"date"}
              is24Hour={true}
              onChange={onDateChangeHandler}
            />
          </View>
          <View style={{ width: "20%" }}></View>
        </View>
      )}
      {!hideTime && (
        <View
          key="time-picker"
          style={{ display: "flex", flexDirection: "row" }}
        >
          <View style={{ width: "40%", margin: 7 }}>
            <Text>Time:</Text>
          </View>
          <View style={{ width: "30%" }}>
            <DateTimePicker
              testID="dateTimePicker"
              value={time}
              mode={"time"}
              is24Hour={true}
              onChange={onTimeChangeHandler}
              style={{ width: "100%" }}
            />
          </View>
          <View style={{ width: "30%" }}></View>
        </View>
      )}
    </View>
  );
}
