export default class DateTimeModel {
  date;
  constructor(date) {
    if(!date) {
      this.date = new Date();
      this.date.setHours(9, 0, 0, 0);
    } else {
      this.date = date;
    }
  }

  getDate() {
    return this.date;
  }

  getDateByRepeat() {
    return this.date;
  } 

  getTimeByRepeat() {
    return this.date;
  } 
  
  getHoursMinutes() {
    return ("0" + this.date.getHours()).slice(-2) + ":" + ("0" + this.date.getMinutes()).slice(-2);
  }

  getDateString() {
    return this.date.toLocaleDateString("en-US");
  }
}
