import { IconButton, Button } from "@react-native-material/core";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";

const Chip = (props) => {
  const { title, onButtonPress, onIconPress, titleStyle, ...others } = props;

  return (
    <Button
      variant="outlined"
      title={title}
      titleStyle={titleStyle}
      onPress={onButtonPress}
      trailing={(props) => (
        <IconButton
          key="add-category"
          onPress={onIconPress}
          icon={(props) => <Icon name="close" size={15} />}
        />
      )}
      {...others}
    />
  );
};

export default Chip;
