import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { Text } from "@react-native-material/core";
import * as React from "react";
import { Image, Pressable, StyleSheet, View } from "react-native";
import { layoutConstants } from "../../models/contants";
import { requestUrlS3 } from "../../services/utils";

export default function BoxLabel({
  style,
  label,
  textStyle,
  onPress,
  icon,
  awsUrl,
}) {
  const [imageUrl, setImageUrl] = React.useState(null);

  React.useEffect(() => {
    if (awsUrl) {
      requestUrlS3(awsUrl).then((imageUrl) => setImageUrl(imageUrl));
    }
  }, [])
  
  return (
    <Pressable style={style ? style : styles.box} onPress={onPress}>
      <View style={{ alignItems: "center", justifyContent: "center", flex: 1 }}>
        <View
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          {imageUrl && (
            <Image style={styles.image} source={{ uri: imageUrl }} />
          )}
          <Text style={textStyle ? textStyle : styles.text}>{label || ""}</Text>
        </View>
      </View>
      <View
        style={{ display: "flex", flexDirection: "row-reverse", width: "100%" }}
      >
        {icon && (
          <Icon
            name={icon}
            color={layoutConstants.colorPrimary}
            size={25}
            style={{ alignItems: "flex-start" }}
          ></Icon>
        )}
      </View>
    </Pressable>
  );
}
const styles = StyleSheet.create({
  box: {
    width: 150,
    height: "45%",
    borderRadius: 10,
    margin: 5,
    padding: 5,
    backgroundColor: layoutConstants.bgColorSecondary,
  },
  text: {
    color: layoutConstants.textColor,
  },
  image: {
    width: 60,
    height: 60,
    borderRadius: 10,
  },
});
