import { Provider } from "@react-native-material/core";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { Amplify } from "aws-amplify";
import * as React from "react";
import { Provider as ReduxProvider } from "react-redux";
import HomeScreen from "screens/home-screen";
import QuoteDetailScreen from "screens/quote-detail-screen";
import TaskScreen from "screens/task-screen";
import TaskDetailScreen from "./app/screens/task-detail-screen";
import store from "./app/store";
import { layoutConstants } from "./app/models/contants";
import CategoryDetailScreen from "./app/screens/category-detail-screen";

Amplify.configure({
  Auth: {
    identityPoolId: "us-east-1:83dc82dd-b63b-4c90-9bd2-068bbec672e0", //REQUIRED - Amazon Cognito Identity Pool ID
    region: "us-east-1", // REQUIRED - Amazon Cognito Region
  },
  Storage: {
    AWSS3: {
      bucket: "goals7d96afe7e9a348eab91826d84ddd7b25221021-dev", //REQUIRED -  Amazon S3 bucket name
      region: "us-east-1", //OPTIONAL -  Amazon service region
    },
  },
});

const Stack = createNativeStackNavigator();

export default function App() {
  const config = {
    screens: {
      QuoteDetail: "quote",
      Actions: "actions",
      Home: "home",
    },
  };

  const linking = {
    prefixes: ["https://elearning01.github.io"],
    config,
  };

  return (
    <ReduxProvider store={store}>
      <Provider>
        <NavigationContainer linking={linking}>
          <Stack.Navigator>
            <Stack.Screen
              name="Home"
              component={HomeScreen}
              options={{
                headerShown: false,
              }}
            />
            <Stack.Screen
              name="Actions"
              component={TaskScreen}
              options={{
                headerStyle: { backgroundColor: layoutConstants.colorPrimary },
                title: "Actions",
                headerTitleAlign: "center",
              }}
            />
            <Stack.Screen
              name="ActionDetail"
              component={TaskDetailScreen}
              options={{
                headerStyle: { backgroundColor: layoutConstants.colorPrimary },
                title: "Action Detail",
                headerTitleAlign: "center",
              }}
            />
            <Stack.Screen
              name="ActionCategoryDetail"
              component={CategoryDetailScreen}
              options={{
                headerStyle: { backgroundColor: layoutConstants.colorPrimary },
                title: "Action Category",
                headerTitleAlign: "center",
              }}
            />
            <Stack.Screen
              name="QuoteDetail"
              component={QuoteDetailScreen}
              options={{
                headerStyle: { backgroundColor: layoutConstants.colorPrimary },
                title: "Quote",
                headerTitleAlign: "center",
              }}
            />
          </Stack.Navigator>
        </NavigationContainer>
      </Provider>
    </ReduxProvider>
  );
}
