import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { IconButton, Text } from "@react-native-material/core";
import { View } from "react-native";

export default function EmptyPage(props) {
  const { onPress } = props;
  return (
    <View style={{ backgroundColor: "white", flex: 1, alignItems: "center", justifyContent: "center" }}>
      <IconButton icon={props => <Icon  size={50} name="plus" />}  onPress={onPress}/>
      <Text style={{padding: 30, color: "grey", fontSize:18}}>Click to create task, You have no task</Text>
    </View>
  );
}
