import { setActiveCategory } from "features/active-category-slice";
import { setCategories } from "features/category-slice";
import { batch, useDispatch } from "react-redux";
import { log } from "utils/log";
import { getTasksByCategory } from "../features/task-slice";
import TaskCategoryDao from "../models/category/task-category-dao";
import TaskCategoryModel from "../models/category/task-category-model";
import {
  CategoryListValidator,
  CategoryValidator,
} from "../models/category/task-category-validator";
import { constants } from "../models/contants";

export default class TaskCategoryService {
  categoryDao;
  defaultCategory;
  dispatch;
  constructor() {
    this.categoryDao = new TaskCategoryDao();
    this.defaultCategory = new TaskCategoryModel({
      id: constants.MY_ACTIONS,
      title: "My Actions",
    });
    this.dispatch = useDispatch();
  }
  async getCategories(includeDeleted = false) {
    log.debug("getCategories", includeDeleted);
    try {
      let categories = JSON.parse(await this.categoryDao.getCategories());
      if (!includeDeleted) {
        categories = categories?.filter((c) => !c.deleted);
      }
      return categories.map((c) => new TaskCategoryModel(c));
    } catch (error) {
      log.warn("getCategories warn", error);
      return [];
    }
  }

  // Get data from store and set to redux setCategories setActiveCategory
  async getCategoriesWithDefault(includeDeleted = false) {
    log.debug("getCategoriesWithDefault", includeDeleted);
    let categories;
    try {
      categories = await this.getCategories(includeDeleted);
      categories = [this.defaultCategory, ...categories];
    } catch (error) {
      log.warn("getCategoriesWithDefault error", error);
      categories = [this.defaultCategory];
    }
    return categories;
  }

  setActiveCategory(category) {
    // should validate
    CategoryValidator.validateSync(category);
    return this.dispatch(setActiveCategory(category));
  }

  setCategories(categories) {
    return this.dispatch(setCategories(categories));
  }

  setActiveCategoriesAndLoadTasks(category) {
    batch(() => {
      this.dispatch(
        getTasksByCategory({
          categoryId: category.id,
          includeDeleted: false,
          category,
        })
      );
      this.dispatch(setActiveCategory(category));
    });
  }

  async saveCategories(categoryModel, isUpdate) {
    let categories;
    if (isUpdate) {
      categories = await this.updateCategory(categoryModel);
    } else {
      categories = await this.addCategories(categoryModel);
    }
    // validate categories
    CategoryListValidator.validate(categories);
    await this.categoryDao.setCategories(JSON.stringify(categories));
    this.setCategories([this.defaultCategory, ...categories], categoryModel);
  }

  async addCategories(categoryModel) {
    try {
      // validate category
      const categories = await this.getCategories();
      categories.push(categoryModel);
      return categories;
    } catch (error) {
      log.error(error);
    }
  }

  async removeCategories(category) {
    log.debug("removeCategories", category);
    try {
      // validate category
      CategoryValidator.validate(category);
      let categories = await this.getCategories(true);
      categories = categories.filter(
        (c) => c.id !== category.id && c.deleted !== true
      );
      // validate categories
      CategoryListValidator.validate(categories);
      await this.categoryDao.setCategories(JSON.stringify(categories));
    } catch (error) {
      log.error("removeCategories", error);
    }
  }

  async softRemoveCategories(category) {
    log.debug("softRemoveCategories", category);
    try {
      // validate category
      CategoryValidator.validate(category);
      let categories = await this.getCategories();
      categories = categories.map((c) => {
        if (c.id === category.id) {
          c.deleted = true;
        }
        return c;
      });
      // validate categories
      CategoryListValidator.validate(categories);
      await this.categoryDao.setCategories(JSON.stringify(categories));
      this.setCategories([this.defaultCategory, ...categories]);
      log.info("softRemoveCategories success");
    } catch (error) {
      log.error("softRemoveCategories", error);
    }
  }

  async undoRemovedCategory({ item, dispatch }) {
    log.debug("undoRemovedCategory", item);
    try {
      // validate category
      CategoryValidator.validate(item);
      let categories = await this.getCategories(true);
      categories = categories.map((c) => {
        if (c.id == item.id) {
          c.deleted = false;
        }
        return c;
      });
      // validate categories
      CategoryListValidator.validate(categories);
      await this.categoryDao.setCategories(JSON.stringify(categories));
      dispatch &&
        dispatch(setCategories([this.defaultCategory, ...categories]));
      return { item };
    } catch (error) {
      log.error("undoRemovedCategory", error);
    }
  }

  async updateCategory(categoryModel) {
    try {
      // validate
      let categories = await this.getCategories();
      categories = categories.map((c) => {
        if (c.id == categoryModel.id) {
          c = categoryModel;
        }
        return c;
      });
      return categories;
    } catch (error) {
      log.error("updateCategory", error);
    }
  }
}

let taskCategoryService;
const createTaskCategoryService = () => {
  if (taskCategoryService) {
    return taskCategoryService;
  }
  taskCategoryService = new TaskCategoryService();
  return taskCategoryService;
};

export { createTaskCategoryService };
