import { log } from "components/utils/log";
import { saveTasksByCategory, setTasks } from "features/task-slice";
import { useDispatch } from "react-redux";
import { setActiveCategory } from "../features/active-category-slice";
import { getTasksByCategory, saveRecent } from "../features/task-slice";
import TaskDao from "../models/task/task-dao";
import { TaskListModel } from "../models/task/task-list-model";
import {
  TaskListValidator,
  TaskValidator,
} from "../models/task/task-validator";

export default class TaskService {
  taskDao;
  dispatch;
  constructor() {
    this.taskDao = new TaskDao();
    this.dispatch = useDispatch();
  }

  async getTasksByCategory(categoryId, includeDeleted = false) {
    log.debug("getTasksByCategory", categoryId, includeDeleted);
    try {
      let rawTasks = JSON.parse(
        await this.taskDao.getTasksByCategory(categoryId)
      );
      const taskList = new TaskListModel();
      taskList.set(rawTasks);
      return taskList;
    } catch (error) {
      log.warn(error);
      return [];
    }
  }

  async getTasksByCategoryStore(categoryId, includeDeleted = false) {
    log.debug("getTasksByCategoryStore", categoryId, includeDeleted);
    try {
      this.dispatch(getTasksByCategory({ categoryId, includeDeleted }));
    } catch (error) {
      log.warn(error);
      return [];
    }
  }

  async setTasksStore(tasks) {
    // validate tasks
    await TaskListValidator.validate(tasks);
    this.dispatch(setTasks(tasks));
  }

  async setTasksByCategory(categoryId, value) {
    log.debug("setTasksByCategory", categoryId, value);
    try {
      await TaskListValidator.validate(value);
      await this.taskDao.setTasksByCategory(categoryId, JSON.stringify(value));
      log.info("setTasksByCategory success");
    } catch (error) {
      log.error(error);
    }
  }

  // save flow: categoryId equal to task.categoryId
  // update flow: categoryId is new category, task.categoryId is old category
  async saveTaskByCategory({categoryId, item, isUpdate, dispatch}) {
    log.debug("saveTaskByCategory", categoryId, item, isUpdate);
    let tasks;
    if (isUpdate) {
      // if new categoryId equal to old categoryId
      if (categoryId === item.categoryId) {
        tasks = await this.updateTaskByCategory({categoryId, item, dispatch});
      } else {
        // remove item in old category
        await this.removeTaskByCategory({categoryId: item.categoryId, item, dispatch});
        // add item in new categoryId
        item.categoryId = categoryId;
        tasks = await this.addTaskByCategory({categoryId, item, dispatch});
      }
    } else {
      tasks = await this.addTaskByCategory({categoryId, item, dispatch});
    }
    // dispatch && dispatch(saveTasksByCategory({ categoryId, tasks }));
    dispatch && dispatch(saveRecent({ recent: item, isUpdate }));
  }

  async removeTaskByCategory({categoryId, item, dispatch}) {
    log.debug("removeTaskByCategory", categoryId, item);
    try {
      await TaskValidator.validate(item);
      let taskList = await this.getTasksByCategory(categoryId);
      taskList.remove(item);
      dispatch && dispatch(saveTasksByCategory({ categoryId, tasks: taskList.get() }));
      return item;
    } catch (error) {
      log.error(error);
    }
  }

  async undoRemovedTask({item, dispatch}) {
    log.debug("undoRemovedTask", item);
    try {
      await TaskValidator.validate(item);
      const categoryId = item.categoryId;
      let taskList = await this.getTasksByCategory(categoryId);
      taskList.undo(item);
      const tasks = taskList.get();
      dispatch && dispatch(saveTasksByCategory({ categoryId, tasks }));
      return {item, list: tasks};
    } catch (error) {
      log.error(error);
    }
  }

  async softRemoveTaskByCategory(categoryId, value) {
    log.debug("softRemoveTaskByCategory", categoryId, value);
    try {
      await TaskValidator.validate(value);
      let taskList = await this.getTasksByCategory(categoryId);
      let index = taskList.softDelete(value);
      if (index == -1) {
        throw Error("not found " + JSON.parse(value));
      }
      this.dispatch(
        saveTasksByCategory({ categoryId, tasks: taskList.get(true) })
      );
      log.info("softRemoveTaskByCategory success");
    } catch (error) {
      log.error(error);
    }
  }

  async updateTaskByCategory({categoryId, item, dispatch}) {
    log.debug("updateTaskByCategory", categoryId, item);
    try {
      await TaskValidator.validate(item);
      let taskList = await this.getTasksByCategory(categoryId);
      const tasks = taskList.update(item);
      dispatch && dispatch(saveTasksByCategory({ categoryId, tasks }));
      return tasks;
    } catch (error) {
      // todo handle error
      log.error(error);
    }
  }

  async addTaskByCategory({categoryId, item, dispatch}) {
    log.debug("addTaskByCategory", categoryId, item);
    try {
      await TaskValidator.validate(item);
      const taskList = await this.getTasksByCategory(categoryId);
      const tasks = taskList.add(item);
      dispatch && dispatch(saveTasksByCategory({ categoryId, tasks }));
      return tasks;
    } catch (error) {
      // todo handle error
      log.error(error);
    }
  }
}

let taskService;
const createTaskService = () => {
  if (taskService) {
    return taskService;
  }
  taskService = new TaskService();
  return taskService;
};

export { createTaskService };
