import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { Button, IconButton } from "@react-native-material/core";
import { HeaderBackButton } from "@react-navigation/elements";
import BoxLabel from "components/home/box-label";
import React, { useEffect, useState } from "react";
import { ImageBackground, Share, StyleSheet, View } from "react-native";
import Swiper from "react-native-swiper/src";
import { log } from "../components/utils/log";
import { constants, layoutConstants } from "../models/contants";
import { createQuoteService } from "../services/quote-service";
import * as Clipboard from "expo-clipboard";
import { useIsFocused } from "@react-navigation/native";
import { ViewLandscape } from "../components/utils/view-landscape";

const QuoteDetailScreen = ({ navigation, route }) => {
  const url = route?.params?.url;
  const quote = route?.params?.quote;
  const owner = route?.params?.owner;
  const current = route?.params?.current;
  const [quotes, setQuotes] = useState([]);
  const quoteService = createQuoteService();
  const [pagination, setPagination] = React.useState();
  const [awsBgUrl, setAwsBgUrl] = React.useState();
  const forcus = useIsFocused();
  const baseBgUrl = "assets/quote/images/";
  const bgUrls = [
    baseBgUrl + "bg_01.png",
    baseBgUrl + "bg_02.png",
    baseBgUrl + "bg_03.png",
    baseBgUrl + "bg_04.png",
    baseBgUrl + "bg_05.png",
    baseBgUrl + "bg_06.png",
  ];
  /**  Math random = 0 => 1
   * expect output 0 => 5 (bgUrls[0] ... bgUrls[5])
   * array length 6
   * random * length = 0 => 6 => should round floor
  */
  const random = Math.floor(Math.random() * bgUrls.length);
  const bgUrl = bgUrls[random];

  useEffect(() => {
    if (forcus && url) {
      getQuotes(url);
    }
    if (!forcus) {
      setQuotes([]);
    } else {
      getBg();
    }
  }, [forcus]);

  useEffect(() => {
    navigation.setOptions({
      headerLeft: (props) => (
        <HeaderBackButton
          {...props}
          onPress={() => {
            navigation.navigate(constants.HOME_SCREEN);
          }}
          label={"Home"}
          labelVisible={true}
          tintColor={layoutConstants.colorPrimary}
        />
      ),
    });
  }, []);

  const getBg = async () => {
    const awsUrl = await quoteService.getBgUrl(bgUrl);
    setAwsBgUrl(awsUrl);
  };

  const getQuotes = async (url) => {
    log.debug("getQuotes", url);
    const data = await quoteService.getList(url);
    let newQuotes = [...quotes, ...data.contents];
    if(current) {
      newQuotes = [newQuotes[current], ...newQuotes?.slice(0, current ), ...newQuotes?.slice(current + 1)];
    }
    setQuotes(newQuotes);
    setPagination(data.pagination);
    log.debug("QuoteDetailScreen", data);
  };

  const onCopy = (quote) => {
    const url =
      constants.CLIENT_URL +
      `/quote?quote=${encodeURIComponent(
        quote.item
      )}&owner=${encodeURIComponent(quote.owner)}`;
    Clipboard.setStringAsync(`${quote.item}\n${url}`);
  };

  const onShare = async (quote) => {
    const url =
      constants.CLIENT_URL +
      `/quote?quote=${encodeURIComponent(
        quote.item
      )}&owner=${encodeURIComponent(quote.owner)}`;
    await Share.share({
      message: url,
    });
  };

  const onLoadMore = (next) => {
    const path = url.replace(
      pagination.current + "/data.json",
      next + "/data.json"
    );
    getQuotes(path);
  };

  const quoteElement = (quote, index = 1, isLast = false) => {
    return (
      <View key={index} style={{ flex: 1, alignItems: "center" }}>
        <View
          style={{
            backgroundColor: "#FFFFFF",
            opacity: 0.5,
            borderRadius: 5,
            flex: 1,
            justifyContent: "center",
            marginHorizontal: 15,
            marginTop: 15,
          }}
        >
          <BoxLabel
            style={styles.item}
            label={quote.item}
            textStyle={styles.textStyle}
          ></BoxLabel>
          <BoxLabel
            label={quote.owner || "Owner"}
            style={styles.owner}
            textStyle={styles.ownerStyle}
          ></BoxLabel>
        </View>

        <View style={{ display: "flex", margin: 10 }}>
          <View style={{ display: "flex", flexDirection: "row" }}>
            <IconButton
              style={{ marginHorizontal: 5, marginBottom: 5 }}
              onPress={() => onShare(quote)}
              contentContainerStyle={styles.socialButton}
              icon={(props) => <Icon name="share" {...props} color={"black"} />}
            />
            <IconButton
              style={{ marginHorizontal: 5, marginBottom: 5 }}
              onPress={() => onCopy(quote)}
              contentContainerStyle={styles.socialButton}
              icon={(props) => (
                <Icon name="content-copy" {...props} color={"black"} />
              )}
            />
          </View>
          {isLast &&
            pagination &&
            pagination.current < pagination.total - 1 && (
              <View style={{ display: "flex", flexDirection: "row" }}>
                <Button
                  title="Load More"
                  onPress={() => onLoadMore(pagination.current + 1)}
                  color={layoutConstants.colorPrimary}
                  tintColor={"white"}
                  style={{ marginBottom: 5 }}
                ></Button>
                {/* <Button
                  title={pagination.current+1+"/"+pagination.total}
                  style={{ marginBottom: 5,marginHorizontal:50}}

                ></Button> */}
              </View>
            )}
        </View>
      </View>
    );
  };

  return (
    <ViewLandscape noScroll={true}>
      {awsBgUrl && (
        <ImageBackground
          source={{ uri: awsBgUrl }}
          resizeMode="cover"
          style={{ flex: 1 }}
        >
          {quotes?.length > 0 && (
            <Swiper horizontal={false} loop={false}>
              {quotes.map((q, index) => {
                return quoteElement(q, index, index === quotes.length - 1);
              })}
            </Swiper>
          )}
          {!quotes?.length && quote && quoteElement({ item: quote, owner })}
        </ImageBackground>
      )}
    </ViewLandscape>
    
  );
};

const styles = StyleSheet.create({
  textStyle: {
    fontSize: layoutConstants.quoteSize,
    paddingHorizontal: 20,
    color: "black",
    fontWeight: "600",
  },
  ownerStyle: {
    fontSize: 20,
    paddingHorizontal: 20,
    color: "black",
  },
  socialButton: { backgroundColor: "white", opacity: 0.7 },
  item: {
    width: "100%",
    justifyContent: "center",
    flex: 3,
  },
  owner: {
    width: "100%",
    alignItems: "flex-end",
    flex: 1,
  },
  viewPager: {
    flex: 1,
  },
  page: {
    justifyContent: "center",
    alignItems: "center",
  },
});
export default QuoteDetailScreen;
