import React, { useEffect } from "react";
import { View } from "react-native";
import { Snackbar, Button } from "@react-native-material/core";
import { log } from "utils/log";

let timeout;
const RemovedSnackBar = (props) => {
  const { item, onUndo, visible, setVisible, onDone } = props;

  const onUndoHandler = () => {
    clearTimeout(timeout);
    onUndo(item);
    setVisible(false);
  }
  
  useEffect(() => {
    if(visible) {
      log.debug('RemovedSnackBar', item)
      timeout = setTimeout(() => {
        onDone(item);
        setVisible(false);
        
      }, 4000);
    }
  }, [visible])
  
  return (
    visible && <View style={{ flex: 1 }}>
      <Snackbar
        message="Removed Item"
        action={<Button variant="text" title="Undo" color="#6666ff" compact onPress={onUndoHandler}/>}
        style={{ position: "absolute", start: 16, end: 16, bottom: 70 }}
      />
    </View>
  );
};

export default RemovedSnackBar;
