import { Text } from "@react-native-material/core";
import * as React from "react";
import { Pressable, StyleSheet, View } from "react-native";
import ExpoCheckbox from "../utils/expo-checkbox";
import { layoutConstants } from "../../models/contants";
import { log } from "../utils/log";

export default function TaskList(props) {
  const { tasks, onPress } = props;
  return (
    <View style={{ flex: 1 }}>
      {tasks &&
        tasks.map((task, index) => {
          const date = task.date ? new Date(task.date) : "";
          const time = task.time ? new Date(task.time) : "";
          let timeDisplay;
          if(date || time) {
            if(!date) {
              timeDisplay = time;
            }
            if(!time) {
              timeDisplay = date;
            }
            if(date && time) {
              timeDisplay = date;
              timeDisplay.setHours(time.getHours());
              timeDisplay.setMinutes(time.getMinutes());
            }
          }
          return (
            <Pressable
              onPress={() => {
                onPress(task);
              }}
              key={task.id + index}
            >
              <View style={[styles.listItem]}>
                <ExpoCheckbox
                  style={{ borderRadius: 40, padding: 12 }}
                  value={false}
                  keyword={task.title}
                  onValueChange={() => {
                    log.debug('Need to implement')
                  }}
                />
                <View style={{ width: "100%", paddingLeft: 10, margin: 5 }}>
                    <Text style={styles.description}>{task.title}</Text>
                    <Text style={styles.description}>{task.description}</Text>
                    <Text style={styles.time}>{timeDisplay && timeDisplay.toLocaleDateString("en-US")}</Text>
                  </View>
              </View>
            </Pressable>
          );
        })}
    </View>
  );
}

const styles = StyleSheet.create({
  listItem: {
    display: "flex",
    flexDirection: "row",
    borderBottomColor: "rgb(216, 216, 216)",
    borderBottomWidth: 2,
    paddingLeft: 15,
    alignItems: "center",
    minHeight: 50,
  },
  description: {
    color: layoutConstants.textColor,
    fontSize: 12
  },
  time: {
    color: 'green',
    fontSize: 12
  }
});
