import { ScrollView, StyleSheet, View } from "react-native";
import { useOrientation } from "./useOrientation";
import { constants, layoutConstants } from "../../models/contants";

export function ViewLandscape({ children, noScroll }) {
  const direction = useOrientation();
  const isLandscape = direction === constants.LANDSCAPE;
  const containerStyle = [styles.container];
  isLandscape && containerStyle.push({ width: "70%", margin: "auto" });
  return (
    <View
      style={{
        width: "100%",
        height: "100%",
        backgroundColor: "white",
        display: "flex",
      }}
    >
      {!!noScroll ? (
        <View style={containerStyle}>{children}</View>
      ) : (
        <ScrollView contentContainerStyle={{ minHeight: "100%" }}>
          <View style={containerStyle}>{children}</View>
        </ScrollView>
      )}
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    color: layoutConstants.colorPrimary,
  },
});
